.main-nav__toggle {
  position: absolute;
  right: 2px;
  top: 0;
  bottom: 0;
  width: 60px;
  height: 55px;
  cursor: pointer;
  border: none;
  background: none;
}
.main-nav__toggle:hover {
  opacity: 0.7;
}
.main-nav__toggle:active {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 18px;
  width: 24px;
  height: 4px;
  background-color: #81B3D2;
  box-shadow: 0 9px 0 0 #81B3D2, 0 18px 0 0 #81B3D2;
}
.main-nav--open::before,
.main-nav--open::after {
  content: "";
  position: absolute;
  top: 26px;
  left: 22px;
  width: 24px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--open::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--open::after {
  transform: rotate(-45deg);
}
.main-nav--open:active::before,
.main-nav--open:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
